<template>
    <div class="freight-request">
        <adt-search-folder
            :searchRouter="{query: {
                freightReqStatus: statusValue,
                departurePort: search.departurePort,
                trsspmPort: search.trsspmPort,
                destinationPort: search.destinationPort
                }}"
            :isStatus="true">
            <template slot="status">
                <div class="status">
                    <div class="status-left">
                        <p><i>*</i> 货运要求状态:</p>
                        <span v-for="(status, id) in auditStatusList" :key="id" @click="auditStatus(id,status)" :class="{'click-status': auditStatusNum == id}">
                            {{status.dictionaryValue}}
                        </span>
                    </div>
                </div>
            </template>
            <template slot="always-show">
                <div class="public-transit">
                    <Form ref="search" :model="search" inline label-colon>
                        <FormItem label="启运地" class="min-width">
                            <adt-city-selector @on-change="handelDirStart"></adt-city-selector>
                        </FormItem>
                        <FormItem label="选择场所">
                            <Select v-model="search.departurePort" filterable clearable>
                                <Option
                                    v-for="item in departurePort"
                                    :value="item.value"
                                    :key="item.value"
                                >{{ item.label }}</Option>
                            </Select>
                        </FormItem>
                        <FormItem label="中转地" class="min-width">
                            <adt-city-selector @on-change="handelDirTrsspmPort"></adt-city-selector>
                        </FormItem>
                        <FormItem label="选择场所">
                            <Select v-model="search.trsspmPort" filterable clearable>
                                <Option
                                    v-for="item in trsspmPort"
                                    :value="item.value"
                                    :key="item.value"
                                >{{ item.label }}</Option>
                            </Select>
                        </FormItem>
                        <br>
                        <FormItem label="目的地" class="min-width">
                            <adt-city-selector @on-change="handelDirEnd"></adt-city-selector>
                        </FormItem>
                        <FormItem label="选择场所">
                            <Select v-model="search.destinationPort" filterable clearable>
                                <Option
                                    v-for="item in destinationPort"
                                    :value="item.value"
                                    :key="item.value"
                                >{{ item.label }}</Option>
                            </Select>
                        </FormItem>
                        <FormItem label="运输类型">
                            <Select v-model="search.trspType" clearable>
                                <Option v-for="(item, index) in trspType" :key="index" :value="item.dictionaryKey">{{ item.dictionaryValue }}</Option>
                            </Select>
                        </FormItem>
                        <FormItem label="货物类型">
                            <Select v-model="search.cargoType" clearable>
                                <Option v-for="(item, index) in cargoType" :key="index" :value="item.dictionaryKey">{{ item.dictionaryValue }}</Option>
                            </Select>
                        </FormItem>
                    </Form>
                </div>
            </template>
        </adt-search-folder>
        <div class="btns-paginator">
            <!-- <AdtButton :allDelShow="true" :selectList="selectList"> -->
            <AdtButton>
                <Button @click="addFreight">新建货运要求<Icon type="ios-add-circle-outline" /></Button>
            </AdtButton>
            <adt-paginator :currentPage="~~page" :total="~~total" />
        </div>
        <div class="public-component-line"></div>
        <div class="public-list">
            <Col class="public-component-card public-card-width" span="24" v-for="(product, key) in listData" :key="key">
                <adt-list-item :data="product" :showCheckbox="true" @checkData="checkData">
                    <template slot="tab-title">
                        <span>货运要求编号:{{ product.freightReqCode }}</span>
                    </template>
                    <template slot="tab-operat">
                        <span class="card-status-success" :class="{'card-status-error': product.freightReqStatus == '未同意方案',
                            'card-status': product.freightReqStatus == '取消货运要求' }">
                        {{ product.freightReqStatus }}</span>
                        <Poptip placement="right-start" class="card-icon">
                            <span>编辑要求</span>
                            <Icon type="md-arrow-dropdown" />
                            <div class="card-poptip-font" slot="content">
                                <!-- <div v-if="product.freightReqStatus === '未发送' || '未同意方案' || '取消货运要求'" @click="edit(product)">编辑要求</div> -->
                                <div @click="detail(product)">查看详情</div>
                                <div v-if="product.freightReqStatus === '未发送' || product.freightReqStatus === '未同意方案' || product.freightReqStatus === '取消货运要求'" @click="send(product)">发送要求</div>
                                <div v-if="product.freightReqStatus === '待调度处理' || product.freightReqStatus === '待销售处理' || product.freightReqStatus === '调度已反馈方案' || product.freightReqStatus === '已反馈方案' || product.freightReqStatus === '未同意方案' || product.freightReqStatus === '已同意方案'" @click="cancelFreight(product)">取消要求</div>
                                <div v-if="product.freightReqStatus === '已同意方案'" @click="viewRecommendations(product)">查看推荐方案</div>
                                <div v-if="product.freightReqStatus === '已反馈方案'" @click="ViewLogisticsSolution(product)">查看物流方案</div>
                                <!-- <div v-if="product.freightReqStatus === '未发送' || '取消货运要求'"  @click="remove(product)" class="failure">删除</div> -->
                            </div>
                        </Poptip>
                    </template>
                    <template slot="tab-content">
                        <div class="public-modify-record data-item tab-content">
                        <div class="collapse" v-if="product.freightRouteList">
                            <Collapse accordion>
                                <Panel name="1">
                                    <Row>
                                        <Col span="22">
                                            <ul>
                                                <li v-for="(item, index) of product.freightRouteList[0].roadList" :key="index">
                                                    <span class="path">{{item.departurePort}}</span>
                                                    <span class="desc">{{item.serverParam.logisticsType}}-{{item.serverParam.trspType}}</span>
                                                    <i>>></i>
                                                    <template v-if="index===product.freightRouteList[0].roadList.length-1">
                                                        <span class="path last-path">{{item.destinationPort}}</span>
                                                    </template>
                                                </li>
                                            </ul>
                                            <p class="status">
                                                <Icon v-show="product.freightRouteList[0].matchScheme === '0'" type="ios-information-circle" />
                                                <Icon v-show="product.freightRouteList[0].matchScheme === '1'" type="ios-checkmark-circle" />
                                                {{product.freightRouteList[0].matchScheme === '0'?'未匹配到物流方案': '已匹配到物流方案'}}
                                            </p>
                                        </Col>
                                        <Col span="2"></Col>
                                    </Row>
                                    <div slot="content">
                                        <Row v-for="(itemList, index) of product.freightRouteList" :key="index" class="content-row" v-show="index!==0">
                                            <Col span="22">
                                                <ul>
                                                    <li v-for="(item,key) of itemList.roadList" :key="key">
                                                        <span class="path">{{item.departurePort}}</span>
                                                        <span class="desc">{{item.serverParam.logisticsType}}-{{item.serverParam.trspType}}</span>
                                                        <i>>></i>
                                                        <template v-if="key===itemList.roadList.length-1">
                                                            <span class="path last-path">{{item.destinationPort}}</span>
                                                        </template>
                                                    </li>
                                                </ul>
                                                <p class="status">
                                                    <Icon v-show="itemList.matchScheme === '0'" type="ios-information-circle" />
                                                    <Icon v-show="itemList.matchScheme === '1'" type="ios-checkmark-circle" />
                                                    {{itemList.matchScheme === '0'?'未匹配到物流方案': '已匹配到物流方案'}}
                                                </p>
                                            </Col>
                                        </Row>
                                    </div>
                                </Panel>
                            </Collapse>
                        </div>
                    </div>
                    </template>
                    <template slot="tab-bottom">
                        <div class="tab-bottom">
                            <Row>
                                <Col span="24">录入时间：{{timestampFormat(product.createDatetime)}}</Col>
                            </Row>
                        </div>
                    </template>
                </adt-list-item>
            </Col>
        </div>
    </div>
</template>

<script>
import { apiChangJing } from '@/api/logisticsCommission'
import { apiDictionaryQueryByCacheTypeKey } from '@/api/dictionary'
import paginator from '@/mixins/paginator'
import AdtListItem from '@/components/adt-list-item/Index'
import { apiQueryListFreight, apiSendSale } from '@/api/freight'
import { timestampFormat } from '@/libs/utils'
export default {
    mixins: [paginator],
    components: {
        AdtListItem
    },
    data() {
        return {
            changeText: '展开',
            dataValue: [],
            search: {
                // 启运地
                departurePort: '',
                // 目的地
                destinationPort: '',
                // 中转地
                trsspmPort: '',
                // 运输类型
                trspType: '',
                // 货物类型
                cargoType: '',
            },
            // 启运地下面的场所的集合
            departurePort: [],
            // 中转地下面的场所的集合
            trsspmPort: [],
            // 目的地下面的场所的集合
            destinationPort: [],
            trspType: this.$getDictionary('TRANSPORT_TYPE'),
            cargoType: this.$getDictionary('GOODS_TYPE'),
            freightReqParam: {},
            listData: [],
            selectList: {},
            transitLoading: false,
            transitData: [],
            list: ['加拿大', '北级', '北京', '加州','哈尔滨', '邯郸', '韩国'],
            transitSelect: [],
            checkList: [],
            // search: {},
            auditStatusNum: '0',
            auditStatusList: this.$getDictionary('FREIGHTREQ_STATUS'),
            statusValue: 'UNSENT'
        }
    },
    methods: {
        // 展开收起
        // changeFont () {
        //     if (this.changeText == '展开') {
        //         this.changeText = '收起'
        //     } else {
        //         this.changeText = '展开'
        //     }
        // },
         // 格式化时间
        timestampFormat (timeStamp) {
            return timestampFormat(timeStamp)
        },
        //拿到场景的字典
        _handelDir(val, data) {
            return new Promise((reslove, rejact) => {
                let code = val[val.length - 1]
                let iscode = val[val.length - 2]
                if (
                    iscode == '110000000000' ||
                    iscode == '120000000000' ||
                    iscode == '500000000000' ||
                    iscode == '310000000000'
                ) {
                    code = iscode
                }
                apiChangJing({ city: code }).then(({ data }) => {
                    let arr = data[0]
                    let newArr = arr.map(item => {
                        return {
                            value: item.sttnCode,
                            label: item.sttnName
                        }
                    })
                    reslove(newArr)
                })
            })
        },
        // 拿到启运地的场景字典
        async handelDirStart(data) {
            let arr = await this._handelDir(data.value)
            this.departurePort = arr
        },
        // 拿到中转地的场景字典
        async handelDirTrsspmPort(data) {
            let arr = await this._handelDir(data.value)
            this.trsspmPort = arr
        },
        // 拿到目的地的场景字典
        async handelDirEnd(data) {
            let arr = await this._handelDir(data.value)
            this.destinationPort = arr
        },
        edit(value) {},
        // 查看详情
        detail(value) {
            this.$router.push(`/freight-request/feedback-detail/${value.freightReqCode}`)
            // this.$router.push(`/freight-request/sale-to-edit/${value.freightReqCode}`)
        },
        // 取消货运要求
        cancelFreight(value) {
            console.log(value)
        },
        // 查看物流方案
        ViewLogisticsSolution(value) {
            window.localStorage.setItem('freightData', JSON.stringify(value))
            this.$router.push({
                path: `/logistics-solutions/customer-service-plan/1`,
                query: {
                    sign: 'viewLogisticsSolution'
                }
            })
        },
        // 查看推荐方案
        viewRecommendations(value) {
            window.localStorage.setItem('freightData', JSON.stringify(value))
            this.$router.push({
                path: `/logistics-solutions/customer-service-plan/1`,
                query: {
                    sign: 'viewRecommendations'
                }
            })
        },
        addFreight() {
            this.$router.push(`/freight-request/simple-entry`)
        },
        checkData (data, status) {
            if(data && status) {
                this.checkList.push(data)
            } else if (data && !status) {
                this.checkList.splice(this.checkList.indexOf(data),1)
            }
            // 选中的数据
            this.selectList = {
                params: this.checkList
            }
        },
        auditStatus (index, status) {
            // 控制状态切换传值
            this.auditStatusNum = index
            // 拿到选中的状态，传入查询接口中
            this.statusValue = status.dictionaryKey
        },
        // 点击查询调用查询的接口
        dataInit () {
            this.getData()
        },
        getData () {
            const data = {
                size: 15,
                page: this.page,
                freightReqStatus: this.statusValue,
                ...this.search,
            }
            apiQueryListFreight({ data }).then(res => {
                this.total = res.data.totalElements
                this.listData = res.data.content
            })
        },
        send(value) {
            const data = {
                freightReqCode: value.freightReqCode,
                freightReqStatus: value.freightReqStatus
            }
            apiSendSale({ data }).then(res => {
                this.$Message.info(res.message)
                this.getData()
            })
        }
    }
}
</script>

<style lang="scss" scoped>
/deep/.ivu-collapse > .ivu-collapse-item > .ivu-collapse-header{
    height: 52px;
    line-height: 52px;
}
.status-left{
    i{
        color: $error-color;
        vertical-align: middle;
    }
}
.public-transit{
    .min-width{
        width: 16%;
    }
}
.card-poptip-font{
    div:hover{
        background: #eee;
    }
}
.content-row{
    line-height: 40px;
}
.tab-content{
    .collapse{
        padding: 0;
        .ivu-col-span-2{
            text-align: right;
            padding-right: 30px;
        }
        ul{
            display: inline-block;
            width: 85%;
            overflow-x: auto;
            white-space: nowrap;
            li{
                display: inline-block;
                vertical-align: middle;
                .path{
                    padding-left: 8px;
                    vertical-align: middle;
                }
                .desc{
                    font-size: 12px;
                    padding: 0 8px;
                    color: $light-color;
                    vertical-align: middle;
                }
                i{
                    color: $light-color;
                }
                &:first-child{
                    .path{
                        font-size: 20px;
                        font-weight: 400;
                    }
                }
                &:last-of-type{
                    .last-path{
                            font-size: 20px;
                            font-weight: 400;
                        }
                }
            }
        }
        .status{
            display: inline-block;
            vertical-align: middle;
            color: $light-color;
            float: right;
            font-size: 12px;
            .ivu-icon{
                font-size: 16px;
            }
        }
        /deep/.ivu-collapse-content{
            width: 100%;
            padding: 0;
            padding-left: 25px;
        }
    }
}
.tab-bottom{
    .ivu-col-span-24{
        font-size: 13px;
    }
}
.public-card-width{
    width: 100%!important;
}
span{
    width: 120px;
    line-height: 30px;
}
.search-condition {
    margin-right: 30px;
    span{
        width: 200px!important;
    }
}
.min-width {
    span{
        width: 60px!important;
    }
    .ivu-input-wrapper{
        width: 60%;
    }
}
.ivu-modal-body{
    .search-condition{
        span{
            width: 180px!important;
        }
    }
}
.line{
    border:0.5px dashed $border-color;
    margin: 20px 0;
}

.search-condition{
    display: flex;
}
.modal-code{
    font-size: 14px;
    font-weight: 400;
    span{
        margin-left: 10px;
    }
}
.line-margin {
    margin-left: 30px;
}
.modal-css{
    margin-left: 0;
}
.icon-action{
    margin: 0 4px;
}
</style>
